import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        appType: process.env.APP_TYPE, // 0-1-
        isLogin: false, // 
        isBLR: 0, // ,0-bind,1-login,2-register
        showLoginBox: false, // 
        mobileNbr: '',
        password: '',
        userCode: '',
        tokenCode: '',
        localCity: '',
        latitude: '',
        longitude: '',
        phoneNum: '',
        isChooseCity: false, // 
        zoneId: '2', // id
        from: '', // weixin
        weixin: {
            openId: '',
            code: '',
            htmldata: ''
        },
        global: {
            staticSrc: process.env.BASE_URL, // 
            hftcom: process.env.BASE_URL, // 
            hftcomImgSrc: process.env.IMG_SRC, // 
            hftcomApi: process.env.API_ROOT_DICT, // 
            hftcomClient: '/Api/Client',
            hftcomShop: '/Api/Shop',
            hftcomWeixinCard: '/Api/WeiXinCard',
            hftcomOpenID: '/Admin/OpenId/getUserOpenId',
            hftcomGetUserInfo: '/Api/Shop',
            Daipay: '/Admin/Lcy/Daipay',
            Dipay: '/Admin/Lcy/DiPay',
            getAT: '/Admin/OpenId/getAccessToken'
        },
        CouponListInfo: [], // 
        collectShopList: [], // 
        userCouponList: [], // 
        isbool: true, // 
        searchIsBool: true,
        nearbyIsBool: true, // 
        nearbyShopList: [],  // shop
        searchConponIsBool: true, // 
        searchConponList: [], // shop
        currentOrderInfo: [], // 
        isCancelOrder: false
    },
    getters,
    mutations,
    actions
});
export default store;



