import { mapState } from 'vuex';
export default{
    props: ['shopTypeList'],
    data() {
        return {
        };
    },
    methods: {
        goSearch(item) {
            this.$router.push({path: '/shops',
                query: {
                    'searchTypeName': item.typeZh, //   0  
                    'searchTypeValue': item.typeValue //     
                }});  // 
        }
    },
    computed: {
        ...mapState([
            'global'
        ])
    }
};



